<template>
  <div class="cooperAtion">
    <home-header></home-header>
    <div class="cooperAtionMain">
        <div class="banner"></div>
        <div class="join">
            <div class="joinTitle">合作加盟</div>
            <div class="joinPic"></div>
            <div class="joinTab">
                <div class="btn" @click="processBtn">
                    <span>视力归元合作流程</span>
                    <span class="after"></span>
                </div>
                <div class="btn" @click="supportBtn">
                    <span>视力归元合作支持</span>
                    <span class="after"></span>
                </div>
            </div>
            <div class="jointxt" v-for="(item, index) in jointxt" :key="index">
                <li>
                    <h2>{{item.hTitle}}</h2>
                    <p>{{item.ptxt}}</p>
                </li>
            </div>
        </div>
    </div>
    <home-footer></home-footer>
  </div>
</template>

<script>
import HomeHeader from '@/views/home/header'
import HomeFooter from '@/views/home/footer'

export default {
    name: 'cooperAtion',
    data() {
        return {
            jointxt: [],
            jointxtO: [{
                hTitle: '1.强大的技术支持',
                ptxt: '礼博总部科研团队考察、汲取国际上先进教育理念与脑潜能开发技术，持续投入时间和精力研发新品课程，分中心共享品牌核心技术。'
            },{
                hTitle: '2.专业的培训支持',
                ptxt: '礼博全脑为分中心提供专业的培训并严格考核，包括专业讲师、销售人员、行政人员等，协助团队组建，并给予各阶段的跟踪指导，确保团队的竞争力和稳定性。'
            },{
                hTitle: '3.实用的运营支持',
                ptxt: '提供终端市场运营专业指导，包括选址装修、空间设计、团队建设、薪资架构、渠道建设等系统协助，帮助分中心成功开店盈利。'
            },{
                hTitle: '4.有效的招生支持',
                ptxt: '总部派专家团队亲临分中心现场主持销讲会，现场体验，现场招生，帮助分中心快速实现盈利。'
            },{
                hTitle: '5.全面的物资支持',
                ptxt: '总部统一提供教学物资及宣传资料，确保分中心统一形象，提升品牌价值。'
            },{
                hTitle: '6.区域的保护支持',
                ptxt: '严格的区域保护制度，杜绝恶性竞争，确保分中心的利益。'
            },{
                hTitle: '7.无忧的持续支持',
                ptxt: '总部设立咨询服务中心，随时帮分中心解决各方面问题。定期培训，确保分中心掌握经营、管理封面必要的教学知识和技能。提供管家式服务，终身跟踪指导运营，帮助分中心提升盈利。'
            }],
            process: [{
                hTitle: '1.  项目申请',
                ptxt: '通过拨打电话或在网上填写申请表，向礼博公司总部提出申请。'
            },{
                hTitle: '2.   实地考察',
                ptxt: '礼博公司邀请初步符合条件的客户到公司总部进行实地考察体验。'
            },{
                hTitle: '3.   项目审核',
                ptxt: '大众创业部顾问向公司提交评估报告，由公司运营部审核。'
            },{
                hTitle: '4.   项目评估',
                ptxt: '公司根据申请资料和项目评估报告作出审批结论。'
            },{
                hTitle: '5.   签订合同',
                ptxt: '如项目审批通过，签订合作协议。'
            },{
                hTitle: '6.   全方位培训',
                ptxt: '公司对加盟合伙人进行师资培训，严格考核，永久免费复训。'
            },{
                hTitle: '7.   全方位支持',
                ptxt: '提供选址、工程装修、开业、运营、营销等各方面管家式支持服务。'
            }],
        };
    },
    components: {
        HomeHeader,
        HomeFooter,
    },
    methods: {
        processBtn() {
            this.jointxt = this.process;
        },
        supportBtn() {
            this.jointxt = this.jointxtO;
        }
    },
    mounted() {
        this.jointxt = this.jointxtO;
    }
}
</script>

<style scoped>
 .banner {
    width: 100%;
    height: 550px;
    background-color: #475669;
  }
  .join {
      width: 1200px;
      margin: 0 auto;
      position: relative;
  }
  .joinTab {
    position: absolute;
    left: 0;
    top: 250px;
    width: 200px;
  }
  .joinTab .btn {
    width: 200px;
    height: 55px;
    background-color: #4c4c4c;
    margin-top: 10px;
    color: #fff;
    border-radius: 5px;
    font-size: 14px;
    text-align: center;
    line-height: 55px;
    cursor: pointer;
    overflow: hidden;
    position: relative;
  }
  .joinTab .btn .after{
    content: "";
    position: absolute;
    background: #484848;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    -webkit-transition: all .5s;
    transition: all .3s;
    opacity: 0;
    top: -70px;
    left: 0px;
    transform:scale(0);
  }
  @keyframes warn {
    0% {
        transform: scale(0);
        -webkit-transform: scale(0);
        opacity: 0.0;
        background: #fff;
    }
    
    100% {
        transform: scale(1.5);
        -webkit-transform: scale(1.5);
        opacity: 0.5;
        background-color: #4c4c4c;
    }
  }
  .joinTab .btn span {
      position: absolute;
      z-index: 9999;
      left: 0;
      right: 0;
  }
  .joinTab .btn:hover .after {
    animation: warn .5s ease-out;
  }
  .joinTitle {
    width: 100%;
    line-height: 60px;
    text-align: center;
    font-size: 35px;
    color: blue;
    background-color: #fff;
    margin: 40px 0;
  }
  .joinPic {
      width: 500px;
      height: 345px;
      margin: 0 auto;
      background-color:cyan;
  }
  .jointxt li {
      font-size: 16px;
      color: rgb(73, 110, 232);
      line-height:  1.7;
      margin-top: 30px;
  }
  .jointxt li h2{
      margin-bottom: 15px;
      font-weight: 700;
  }
  .jointxt li p{
      font-size: 14px;
  }
</style>



