import Vue from 'vue'
import Router from 'vue-router'
import IndexHome from '@/views/index'
import AboutApp from '@/views/aboutUs/aboutUs'
import contactApp from '@/views/contactUs/contactUs'
import cooperAtion from '@/views/cooperation/cooperation'
import visionReturn from '@/views/visionReturn/visionReturn'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'IndexHome',
      component: IndexHome
    },
    {
      path: '/aboutUs',
      name: 'AboutApp',
      component: AboutApp
    },
    {
      path: '/contact',
      name: 'contactApp',
      component: contactApp
    },
    {
      path: '/cooperAtion',
      name: 'cooperAtion',
      component: cooperAtion
    },
    {
      path: '/visionReturn',
      name: 'visionReturn',
      component: visionReturn
    }
  ],
  mode: 'history'
})



