<template>
  <div class="aboutApp">
    <home-header></home-header>
    <div class="aboutMain">
        <div class="banner">
            <button>了解详情</button>
        </div>
        <div class="mainPhoto clearfix">
            <div class="aboutTitle">关于我们</div>
            <div class="aboutUs">
                <div class="one fl"></div>
                <div class="two fr">
                    <p>秉承着“用心教学，用心创新，用心服务”的经营理念</p>
礼博是全球领先的脑潜能开发与应用解决方案研发和孵化机构，拥有顶尖的技术、研发和经营管理团队，专注于青少年儿童的全脑潜能学习与应用及青少年视力恢复，提升中小学生的专注力、创造力、记忆力、想象力、执行力，自信心，及恢复青少年视力，从本质上彻底改善孩子视力问题，提高孩子内在的学习能力，大幅减轻学习压力。
                </div>
                <div class="three fl">
                    <p>企业发展足迹</p>
2017年品牌开展加盟创业项目。并在北京成立“中科全脑”重点推出的“视力归元”课程让上千余近视孩子通过课程恢复视力，让孩子重获一双明亮的眼睛，还孩子一个清澈澄明的视界。并在同年推出“盲童复明”工程，帮助失明的孩子重获光明。
                </div>
                <div class="four fr"></div>
            </div>
        </div>
        <div class="schoolStyle">
            <div class="styleMain">
                <div class="styleTitle">
                    <h2>学院风采</h2>
                    <span>SCHOOL OF ELEGANT APPEARANCE</span>
                </div>
                <div class="styleOne"></div>
                <div class="styleTwo"></div>
                <div class="styleThree"></div>
                <div class="styleFour"></div>
                <div class="styleFive"></div>
            </div>
        </div>
        
    </div>
    <home-footer></home-footer>
  </div>
</template>

<script>
import HomeHeader from "@/views/home/header";
import HomeFooter from "@/views/home/footer";

export default {
  name: "AboutApp",
  data() {
    return {};
  },
  components: {
    HomeHeader,
    HomeFooter
  }
};
</script>

<style scoped>
.aboutMain {
  width: 100%;
}
.banner {
  width: 100%;
  height: 700px;
  background-color: #475669;
  position: relative;
}
button {
  position: absolute;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 200px;
  height: 45px;
  background-color: transparent;
  border: 1px solid #fff;
  border-radius: 30px;
  font-size: 16px;
  line-height: 45px;
  color: #fff;
}
button:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.3);
}
.mainPhoto {
  width: 1200px;
  margin: 80px auto;
}
.aboutTitle {
  width: 100%;
  line-height: 60px;
  text-align: center;
  font-size: 35px;
  color: blue;
  background-color: #fff;
  margin: 40px 0;
}
.one,
.four {
  width: 590px;
  height: 350px;
  margin-bottom: 20px;
  background-color: #d3dce6;
  transition: all 1s;
}
p {
    font-size: 18px;
    color: #000;
}
.two, .three {
    width: 590px;
    height: 350px;
    margin-bottom: 20px;
    padding: 50px;
    box-sizing: border-box;
    font-size: 14px;
    line-height: 32px;
    font-weight: 700;
}
.schoolStyle {
    width: 100%;
    height:800px;
    background-color: rgb(243,243,243);
}
.styleMain {
    position: relative;
    width: 1400px;
    height: 100%;
    margin: 0 auto;
    padding: 100px 0;
    box-sizing: border-box;
}
.styleTitle {
    color: rgb(73,110,232);
    line-height: 1.5;
}
.styleTitle h2 {
    font-size: 30px;
    font-weight: 700;
}
.styleTitle span {
    font-size: 16px;
}
.styleOne{
    width: 480px;
    height: 360px;
    background-color: cyan;
    position: absolute;
    left: 0;
    top: 235px;
} 
.styleTwo{
    width: 415px;
    height: 280px;
    background-color: orange;
    position: absolute;
    left: 520px;
    top: 60px;
} 
.styleThree, .styleFour, .styleFive{
    width: 410px;
    height: 310px;
    position: absolute;
} 
.styleThree {
    background-color: blue;
    left: 520px;
    top: 380px;
}
.styleFour {
    background-color: yellow;
    right: 0;
    top: 60px;

}
.styleFive {
    background-color: darkgreen;
    right: 0;
    top: 410px;
}

</style>



