<template>
  <div class="home-main">
    <div class="carPhoto">
      <el-carousel :interval="5000" arrow="always" height="500px">
        <el-carousel-item v-for="item in 4" :key="item">
          <h3>{{ item }}</h3>
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="mainPhoto clearfix">
      <div class="aboutTitle">关于我们</div>
      <div class="aboutUs">
        <div class="one fl"></div>
        <div class="two fr"></div>
        <div class="three fl"></div>
        <div class="four fr"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeMain'
}
</script>

<style scoped>
  .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  .mainPhoto {
    width: 1200px;
    margin: 0 auto;
  }
  .aboutTitle {
    width: 100%;
    line-height: 60px;
    text-align: center;
    font-size: 35px;
    color: blue;
    background-color: #fff;
    margin: 40px 0;
  }
  .one, .two, .three, .four {
    width: 590px;
    height: 350px;
    margin-bottom: 20px;
    background-color: #d3dce6;
    transition: all 1s;
  }
  .one:hover, .two:hover, .three:hover, .four:hover {
    transform: translate(-5px, -5px);
    box-shadow: #ccc 0 5px 5px;
    margin-bottom: 20px;
  }
</style>



