<template>
<div>
  <div class="home-header">
    <div class="left fl">
      <div class="logo fl">视力归元</div>
      <div class="logoDes fl"><span>视力归元</span>中国视力自然疗愈<span>两天</span>恢复好视力跟<span>近视眼</span>说拜拜</div>
    </div>
    <div class="right fr">
      <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1">首页</el-menu-item>
        <el-menu-item index="2">关于我们</el-menu-item>
        <el-menu-item index="3">视力归元</el-menu-item>
        <el-menu-item index="4">合作加盟</el-menu-item>
        <el-menu-item index="5">联系我们</el-menu-item>
      </el-menu>
    </div>
  </div>
  <div class="zhanwei"></div>
</div>
</template>

<script>
export default {
  name: 'HomeHeader',
  data() {
      return {
        activeIndex: '1',
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
        key == 1 ? this.$router.push({path:'/'}) : '';
        key == 2 ? this.$router.push({path:'aboutUs'}) : '';
        key == 3 ? this.$router.push({path:'visionReturn'}) : '';
        key == 4 ? this.$router.push({path:'cooperAtion'}) : '';
        key == 5 ? this.$router.push({path:'contact'}) : '';
      },
    },
    mounted() {
      let path = this.$route.path;
      path == '/' ? this.activeIndex = '1' : '';
      path == '/aboutUs' ? this.activeIndex = '2' : '';
      path == '/visionReturn' ? this.activeIndex = '3' : '';
      path == '/cooperAtion' ? this.activeIndex = '4' : '';
      path == '/contact' ? this.activeIndex = '5' : '';
    },
}
</script>

<style scoped>
.zhanwei {
  width: 100%;
  height: 80px;
}
 .home-header {
   position: fixed;
   top: 0;
   width: 100%;
   height: 60px;
   padding: 20px 0 0 0;
   border-bottom: 1px solid #ccc;
   background-color: #fff;
   z-index: 999;
 }
 .left {
   display: inline-block;
 }
 .right {
   margin-right: 50px;
 }
 .logo {
   margin-left: 40px;
   font-size: 35px;
   line-height: 60px;
   font-weight: 700;
   color: #000;
 }
 .logoDes {
   font-size: 24px;
   line-height: 30px;
   width: 320px;
   margin-left: 20px;
   color: #000;
 }
 .logoDes span {
   font-weight: 700;
   color:red;
 }
</style>



