<template>
  <div class="home-footer">
    <div class="mainDsc clearfix">
      <div class="dscOne fl"></div>
      <div class="dscTwo fl"></div>
      <div class="dscThree fl"></div>
      <div class="dscFour fl"></div>
    </div>
    <div class="footerLine">
      
      我也是有底线的 &nbsp;© Mr Huang 2019
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeFooter'
}
</script>

<style scoped>
  .footerLine {
    width: 90%;
    border-top: 1px solid #ccc;
    margin: 80px auto 20px auto;
    text-align: center;
    color: #ccc;
    line-height: 40px;
  }
  .mainDsc {
    width: 1400px;
    margin: 0 auto;
    padding: 20px 0;
    margin-top: 80px;
  }
  .dscOne, .dscTwo, .dscThree, .dscFour{
    width: 335px;
    height: 500px;
    background-color:#99a9bf;
    margin-right: 20px;
    transition: all 1s;
  }
  .dscOne:hover, .dscTwo:hover, .dscThree:hover, .dscFour:hover{
    transform: translate(-5px, -5px);
    box-shadow: #ccc 0 10px 10px;
  }
  .dscFour {
    margin: 0;
  }
</style>



